<template>
    <div class="form-container">
        <el-form ref="form" :model="form" :rules="rules" label-width="110px" class="form">

            <el-row type="flex" class="row-bg" justify="left">
                <el-form-item label="单日上限总分" prop="onedayScore">
                    <el-radio-group v-model="form.onedayScore">
                        <el-radio class="block" label="-1">无上限 </el-radio>

                        <div style="margin-top: 20px;">
                            <el-radio class="block" label="-2"></el-radio>
                        </div>

                    </el-radio-group>
                </el-form-item>

                <div style=" margin-left: -154px;  margin-top: 32px;">
                    <el-form-item prop="onedayScoreNum">
                        <el-input v-model="form.onedayScoreNum"></el-input>
                    </el-form-item>
                </div>

            </el-row>


            <el-row type="flex" class="row-bg" justify="left">

                <div style="margin-top: 20px;">
                    <el-form-item label="任务上限总分" prop="totalScore">
                        <el-radio-group v-model="form.totalScore">
                            <el-radio class="block" label="-1">无上限 </el-radio>
                            <div style="margin-top: 20px;">
                                <el-radio class="block" label="-2"></el-radio>
                            </div>
                        </el-radio-group>
                    </el-form-item>

                    <div style="margin-left: 23px;    margin-top: -50px;">
                        <el-form-item prop="totalScoreNum">
                            <el-input v-model="form.totalScoreNum"></el-input>
                        </el-form-item>
                    </div>
                </div>

            </el-row>



            <el-form-item>
                <el-button type="primary" @click="commit">提交</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    import { getSetting, updateSetting } from 'api/task';

    export default {
        name: 'taskSettingsModify',
        data() {
            return {
                listLoading: true,
                form: {
                    id:'',
                    onedayScore: '',
                    totalScore: '',
                    onedayScoreNum: '',
                    totalScoreNum: ''
                },

                rules: {
                    onedayScore: [
                        { required: true, message: '请输入单日上限总分', trigger: 'blur' }
                    ],
                    totalScore:[
                        { required: true, message: '请选择任务上限总分', trigger: 'change'}
                    ],
                    onedayScoreNum:[
                        { required: true, message: '请输入单日上限总分,最多输入小数点后两位',pattern:  /^\d+(\.\d{2})?$/, trigger: 'change'}
                    ],
                    totalScoreNum:[
                        { required: true, message: '请选择任务上限总分,最多输入小数点后两位',pattern:  /^\d+(\.\d{2})?$/, trigger: 'change'}
                    ]
                }
            }
        },
        created() {
            this.getSetting();
        },
        methods: {

            getSetting() {
                getSetting().then(response => {
                    if (response.success){

                    var data = response.data[0];
                    this.form.id = data.id;

                    if (data.onedayScore == -1) {
                        this.form.onedayScore = -1 + '';
                    }else{
                        this.form.onedayScore = -2 + '';
                        this.form.onedayScoreNum = data.onedayScore;
                    }

                    if (data.totalScore == -1) {
                        this.form.totalScore = -1 + '';
                    }else{
                        this.form.totalScore = -2 + '';
                        this.form.totalScoreNum = data.totalScore;
                    }

                }else{
                    this.$notify({
                        title: '失败',
                        message: response.message,
                        type: 'error',
                        duration: 2000
                    })
                }
            });
            },
            commit() {
                this.$refs['form'].validate((valid) => {

                    if (valid) {
                        if(this.form.onedayScore==-2 && this.form.onedayScoreNum <= 0){
                            return;
                        }
                        if(this.form.totalScore==-2 && this.form.totalScoreNum <= 0){
                            return;
                        }
                        var param = this.form;
                        if( this.form.onedayScore==-2 && this.form.onedayScoreNum > 0 ){
                            param.onedayScore = this.form.onedayScoreNum;
                        }
                        if( this.form.totalScore==-2 && this.form.totalScoreNum > 0 ){
                            param.totalScore = this.form.totalScoreNum;
                        }

                        updateSetting(param).then(response => {
                            if (response.success) {
                            this.$notify({
                                title: '成功',
                                message: '提交成功',
                                type: 'success',
                                duration: 2000
                            });
                            this.$refs['form'].resetFields();

                            this.$router.push({ path: '/task/taskSettings' });


                        } else {
                            this.$notify({
                                title: '失败',
                                message: response.message,
                                type: 'error',
                                duration: 2000
                            });
                        }
                    })
                    } else {
                        console.log('error submit!!');
                return false;
            }
            });
            }
        }
    };
</script>

<style scoped>
    .form-container{
        padding:15px;
        position: relative;
        width: 100%;
        height: 80%;
    }
    .el-radio.block{
        display: block;
        margin-left: 0;
    }
</style>


