import { fetch } from 'utils/fetch';

 export function fetchList(query) {
   return fetch({
     url: '/bus/task/list.do?' + query,
     method: 'get'
   })
 }

export function fetchUserList() {
    return fetch({
        url: '/sysconfig/getAccounts.do',
        method: 'get'
    })
}

 export function setTaskStatus(id, status) {
   return fetch({
     url: '/bus/task/enable.do',
     method: 'post',
     data: {
         id,
       status
     }
   })
 }

export function addTask(form) {
    return fetch({
        url: '/bus/task/save.do',
        method: 'post',
        data: form
    })
}

export function getSetting() {
    return fetch({
        url: '/bus/taskset/getConfTaskSet.do',
        method: 'get'
    })
}

export function updateSetting(form) {
    return fetch({
        url: '/bus/taskset/save.do',
        method: 'post',
        data:form
    })
}

export function fetchHistory(query) {
    return fetch({
        url:'/bus/taskset/getHisTaskSet.do?' + query,
        method: 'get'
    })
}

